import React, {useCallback} from "react";
import Particles from "react-particles";
import {loadFull} from "tsparticles";

export function ParticlesContainer() {
    const customInit = useCallback(async (engine) => {
        await loadFull(engine);
    });


    return <Particles
        id="tsparticles"
        init={customInit}

        options={{
            "fullScreen": {
                "enable": true, "zIndex": 0
            }, "particles": {
                "number": {
                    "value": 10, "density": {
                        "enable": false, "value_area": 800
                    }
                }, "color": {
                    "value": "#fff"
                }, "opacity": {
                    "value": 0.1, "random": false, "anim": {
                        "enable": false, "speed": 0.5, "opacity_min": 0.05, "sync": false
                    }
                }, "size": {
                    "value": 1, "random": false, "anim": {
                        "enable": false, "speed": 10, "size_min": 0.1, "sync": false
                    }
                }, "rotate": {
                    "value": 0, "random": true, "direction": "clockwise", "animation": {
                        "enable": true, "speed": 3, "sync": false
                    }
                }, "line_linked": {
                    "enable": true, "distance": 600, "color": "#ffffff", "opacity": 0.1, "width": 1
                }, "move": {
                    "enable": true,
                    "speed": 2,
                    "direction": "none",
                    "random": false,
                    "straight": false,
                    "out_mode": "out",
                    "attract": {
                        "enable": false, "rotateX": 600, "rotateY": 1200
                    }
                }
            }, "interactivity": {
                "events": {
                    "onhover": {
                        "enable": true, "mode": ["grab"]
                    }, "onclick": {
                        "enable": false, "mode": "bubble"
                    }, "resize": true
                }, "modes": {
                    "grab": {
                        "distance": 400, "line_linked": {
                            "opacity": 1
                        }
                    }, "bubble": {
                        "distance": 400, "size": 40, "duration": 2, "opacity": 8, "speed": 3
                    }, "repulse": {
                        "distance": 200
                    }, "push": {
                        "particles_nb": 4
                    }, "remove": {
                        "particles_nb": 2
                    }
                }
            }, "retina_detect": true, "background": {
                "color": "#111", "image": "", "position": "50% 50%", "repeat": "no-repeat", "size": "cover"
            }
        }}
    />;
}